import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { updateProfile, updateEmail, updatePassword } from 'firebase/auth';
import { doc, updateDoc } from 'firebase/firestore';
import { auth, db } from '../firebase';

const SettingsContainer = styled.div`
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
`;

const Header = styled.div`
  margin-bottom: 30px;
`;

const Title = styled.h1`
  font-size: 2rem;
  color: #333;
  margin-bottom: 10px;
`;

const Subtitle = styled.p`
  color: #666;
  font-size: 1.1rem;
`;

const Section = styled.div`
  background-color: white;
  border-radius: 10px;
  padding: 20px;
  margin-bottom: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
`;

const SectionTitle = styled.h2`
  font-size: 1.3rem;
  color: #333;
  margin-bottom: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid #eee;
`;

const FormGroup = styled.div`
  margin-bottom: 20px;
`;

const Label = styled.label`
  display: block;
  margin-bottom: 8px;
  color: #555;
  font-weight: 500;
`;

const Input = styled.input`
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 1rem;
  transition: border-color 0.3s;

  &:focus {
    border-color: #4a86e8;
    outline: none;
  }
`;

const Button = styled.button`
  background-color: #4a86e8;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s;

  &:hover {
    background-color: #3a76d8;
  }

  &:disabled {
    background-color: #a2c0f7;
    cursor: not-allowed;
  }
`;

const Switch = styled.label`
  display: flex;
  align-items: center;
  cursor: pointer;
  user-select: none;
`;

const SwitchInput = styled.input`
  display: none;
`;

const SwitchSlider = styled.span`
  position: relative;
  display: inline-block;
  width: 50px;
  height: 24px;
  background-color: #ccc;
  border-radius: 24px;
  margin-right: 10px;
  transition: background-color 0.3s;

  &:before {
    content: '';
    position: absolute;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: white;
    top: 2px;
    left: 2px;
    transition: transform 0.3s;
  }

  ${SwitchInput}:checked + & {
    background-color: #4a86e8;
  }

  ${SwitchInput}:checked + &:before {
    transform: translateX(26px);
  }
`;

const SuccessMessage = styled.div`
  color: #28a745;
  margin-top: 10px;
  font-size: 0.9rem;
`;

const ErrorMessage = styled.div`
  color: #dc3545;
  margin-top: 10px;
  font-size: 0.9rem;
`;

const Settings = () => {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState('');
  const [error, setError] = useState('');
  const [settings, setSettings] = useState({
    displayName: '',
    email: '',
    currentPassword: '',
    newPassword: '',
    confirmPassword: '',
    emailNotifications: true,
    darkMode: false
  });

  useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged((user) => {
      if (user) {
        setUser(user);
        setSettings(prev => ({
          ...prev,
          displayName: user.displayName || '',
          email: user.email || ''
        }));
      }
    });

    return () => unsubscribe();
  }, []);

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    setSettings(prev => ({
      ...prev,
      [name]: type === 'checkbox' ? checked : value
    }));
  };

  const handleProfileUpdate = async (e) => {
    e.preventDefault();
    setLoading(true);
    setSuccess('');
    setError('');

    try {
      //   
      if (settings.displayName !== user.displayName) {
        await updateProfile(user, {
          displayName: settings.displayName
        });
      }

      //  
      if (settings.email !== user.email) {
        await updateEmail(user, settings.email);
      }

      // Firestore   
      const userRef = doc(db, 'users', user.uid);
      await updateDoc(userRef, {
        displayName: settings.displayName,
        email: settings.email,
        updatedAt: new Date().toISOString()
      });

      setSuccess('  .');
    } catch (error) {
      console.error('  :', error);
      setError('    .');
    } finally {
      setLoading(false);
    }
  };

  const handlePasswordUpdate = async (e) => {
    e.preventDefault();
    setLoading(true);
    setSuccess('');
    setError('');

    if (settings.newPassword !== settings.confirmPassword) {
      setError('   .');
      setLoading(false);
      return;
    }

    try {
      await updatePassword(user, settings.newPassword);
      setSuccess('  .');
      setSettings(prev => ({
        ...prev,
        currentPassword: '',
        newPassword: '',
        confirmPassword: ''
      }));
    } catch (error) {
      console.error('  :', error);
      setError('    .');
    } finally {
      setLoading(false);
    }
  };

  const handleNotificationUpdate = async (e) => {
    e.preventDefault();
    setLoading(true);
    setSuccess('');
    setError('');

    try {
      const userRef = doc(db, 'users', user.uid);
      await updateDoc(userRef, {
        emailNotifications: settings.emailNotifications,
        updatedAt: new Date().toISOString()
      });
      setSuccess('  .');
    } catch (error) {
      console.error('   :', error);
      setError('     .');
    } finally {
      setLoading(false);
    }
  };

  const handleThemeUpdate = async (e) => {
    e.preventDefault();
    setLoading(true);
    setSuccess('');
    setError('');

    try {
      const userRef = doc(db, 'users', user.uid);
      await updateDoc(userRef, {
        darkMode: settings.darkMode,
        updatedAt: new Date().toISOString()
      });
      setSuccess('  .');
    } catch (error) {
      console.error('   :', error);
      setError('     .');
    } finally {
      setLoading(false);
    }
  };

  return (
    <SettingsContainer>
      <Header>
        <Title></Title>
        <Subtitle>    </Subtitle>
      </Header>

      <Section>
        <SectionTitle> </SectionTitle>
        <form onSubmit={handleProfileUpdate}>
          <FormGroup>
            <Label></Label>
            <Input
              type="text"
              name="displayName"
              value={settings.displayName}
              onChange={handleChange}
            />
          </FormGroup>
          <FormGroup>
            <Label></Label>
            <Input
              type="email"
              name="email"
              value={settings.email}
              onChange={handleChange}
            />
          </FormGroup>
          <Button type="submit" disabled={loading}>
            {loading ? ' ...' : ' '}
          </Button>
          {success && <SuccessMessage>{success}</SuccessMessage>}
          {error && <ErrorMessage>{error}</ErrorMessage>}
        </form>
      </Section>

      <Section>
        <SectionTitle> </SectionTitle>
        <form onSubmit={handlePasswordUpdate}>
          <FormGroup>
            <Label> </Label>
            <Input
              type="password"
              name="currentPassword"
              value={settings.currentPassword}
              onChange={handleChange}
            />
          </FormGroup>
          <FormGroup>
            <Label> </Label>
            <Input
              type="password"
              name="newPassword"
              value={settings.newPassword}
              onChange={handleChange}
            />
          </FormGroup>
          <FormGroup>
            <Label>  </Label>
            <Input
              type="password"
              name="confirmPassword"
              value={settings.confirmPassword}
              onChange={handleChange}
            />
          </FormGroup>
          <Button type="submit" disabled={loading}>
            {loading ? ' ...' : ' '}
          </Button>
          {success && <SuccessMessage>{success}</SuccessMessage>}
          {error && <ErrorMessage>{error}</ErrorMessage>}
        </form>
      </Section>

      <Section>
        <SectionTitle> </SectionTitle>
        <form onSubmit={handleNotificationUpdate}>
          <FormGroup>
            <Switch>
              <SwitchInput
                type="checkbox"
                name="emailNotifications"
                checked={settings.emailNotifications}
                onChange={handleChange}
              />
              <SwitchSlider />
               
            </Switch>
          </FormGroup>
          <Button type="submit" disabled={loading}>
            {loading ? ' ...' : '  '}
          </Button>
          {success && <SuccessMessage>{success}</SuccessMessage>}
          {error && <ErrorMessage>{error}</ErrorMessage>}
        </form>
      </Section>

      <Section>
        <SectionTitle> </SectionTitle>
        <form onSubmit={handleThemeUpdate}>
          <FormGroup>
            <Switch>
              <SwitchInput
                type="checkbox"
                name="darkMode"
                checked={settings.darkMode}
                onChange={handleChange}
              />
              <SwitchSlider />
               
            </Switch>
          </FormGroup>
          <Button type="submit" disabled={loading}>
            {loading ? ' ...' : '  '}
          </Button>
          {success && <SuccessMessage>{success}</SuccessMessage>}
          {error && <ErrorMessage>{error}</ErrorMessage>}
        </form>
      </Section>
    </SettingsContainer>
  );
};

export default Settings; 