import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { collection, addDoc, getDocs, query, orderBy, deleteDoc, doc, updateDoc } from 'firebase/firestore';
import { db } from '../firebase';

const MessagesContainer = styled.div`
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
`;

const Header = styled.div`
  margin-bottom: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
`;

const Title = styled.h1`
  font-size: 2rem;
  color: #333;
  margin: 0;
`;

const FilterSection = styled.div`
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
`;

const FilterContainer = styled.div`
  display: flex;
  gap: 10px;
  margin-bottom: 15px;
  flex-wrap: wrap;
`;

const FilterButton = styled.button`
  padding: 8px 16px;
  border-radius: 20px;
  background-color: ${({ active }) => (active ? '#4a86e8' : 'white')};
  color: ${({ active }) => (active ? 'white' : '#333')};
  border: 1px solid #4a86e8;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 0.9rem;

  &:hover {
    background-color: ${({ active }) => (active ? '#3a76d8' : '#f0f0f0')};
  }
`;

const MessageForm = styled.form`
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  margin-bottom: 30px;
`;

const FormGroup = styled.div`
  margin-bottom: 15px;
`;

const Label = styled.label`
  display: block;
  margin-bottom: 5px;
  color: #333;
  font-weight: 500;
`;

const Input = styled.input`
  width: 100%;
  padding: 8px 12px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 1rem;
`;

const TextArea = styled.textarea`
  width: 100%;
  padding: 8px 12px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 1rem;
  min-height: 100px;
  resize: vertical;
`;

const SubmitButton = styled.button`
  padding: 10px 20px;
  background-color: #4a86e8;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s;

  &:hover {
    background-color: #3a76d8;
  }

  &:disabled {
    background-color: #a2c0f7;
    cursor: not-allowed;
  }
`;

const MessageGrid = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
`;

const MessageCard = styled.div`
  background-color: white;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  position: relative;
  transition: transform 0.3s ease;

  &:hover {
    transform: translateY(-5px);
  }
`;

const MessageTitle = styled.h3`
  font-size: 1.2rem;
  margin-bottom: 10px;
  color: #333;
`;

const MessageContent = styled.p`
  color: #666;
  line-height: 1.6;
  margin-bottom: 15px;
`;

const MessageMeta = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.9rem;
  color: #888;
`;

const MessageCategory = styled.span`
  display: inline-block;
  padding: 4px 8px;
  background-color: #e8f0fe;
  color: #4a86e8;
  border-radius: 12px;
  font-size: 0.8rem;
`;

const DeleteButton = styled.button`
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  color: #ff4444;
  cursor: pointer;
  font-size: 1.2rem;
  opacity: 0;
  transition: opacity 0.3s;

  ${MessageCard}:hover & {
    opacity: 1;
  }

  &:hover {
    color: #ff0000;
  }
`;

const categories = [
  { id: 'all', name: '' },
  { id: 'love', name: '' },
  { id: 'memory', name: '' },
  { id: 'wish', name: '' },
  { id: 'gratitude', name: '' }
];

const Messages = () => {
  const [messages, setMessages] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState('all');
  const [loading, setLoading] = useState(false);
  const [newMessage, setNewMessage] = useState({
    title: '',
    content: '',
    category: 'love'
  });

  useEffect(() => {
    loadMessages();
  }, [selectedCategory]);

  const loadMessages = async () => {
    setLoading(true);
    try {
      const messagesRef = collection(db, 'messages');
      const q = query(messagesRef, orderBy('date', 'desc'));
      const querySnapshot = await getDocs(q);
      
      let loadedMessages = querySnapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data()
      }));

      if (selectedCategory !== 'all') {
        loadedMessages = loadedMessages.filter(message => message.category === selectedCategory);
      }

      setMessages(loadedMessages);
    } catch (error) {
      console.error('  :', error);
    } finally {
      setLoading(false);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!newMessage.title.trim() || !newMessage.content.trim()) {
      alert('   .');
      return;
    }

    setLoading(true);
    try {
      await addDoc(collection(db, 'messages'), {
        ...newMessage,
        date: new Date().toISOString()
      });

      setNewMessage({
        title: '',
        content: '',
        category: 'love'
      });

      loadMessages();
    } catch (error) {
      console.error('  :', error);
      alert('    .');
    } finally {
      setLoading(false);
    }
  };

  const handleDelete = async (messageId) => {
    if (!window.confirm('  ?')) {
      return;
    }

    try {
      setLoading(true);
      await deleteDoc(doc(db, 'messages', messageId));
      loadMessages();
    } catch (error) {
      console.error('  :', error);
      alert('    .');
    } finally {
      setLoading(false);
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setNewMessage(prev => ({
      ...prev,
      [name]: value
    }));
  };

  return (
    <MessagesContainer>
      <Header>
        <Title> </Title>
        <FilterSection>
          <FilterContainer>
            {categories.map(category => (
              <FilterButton
                key={category.id}
                active={selectedCategory === category.id}
                onClick={() => setSelectedCategory(category.id)}
              >
                {category.name}
              </FilterButton>
            ))}
          </FilterContainer>
        </FilterSection>
      </Header>

      <MessageForm onSubmit={handleSubmit}>
        <FormGroup>
          <Label></Label>
          <Input
            type="text"
            name="title"
            value={newMessage.title}
            onChange={handleChange}
            placeholder="  "
          />
        </FormGroup>
        <FormGroup>
          <Label></Label>
          <Input
            as="select"
            name="category"
            value={newMessage.category}
            onChange={handleChange}
          >
            {categories.filter(cat => cat.id !== 'all').map(category => (
              <option key={category.id} value={category.id}>
                {category.name}
              </option>
            ))}
          </Input>
        </FormGroup>
        <FormGroup>
          <Label></Label>
          <TextArea
            name="content"
            value={newMessage.content}
            onChange={handleChange}
            placeholder="  "
          />
        </FormGroup>
        <SubmitButton type="submit" disabled={loading}>
          {loading ? ' ...' : ' '}
        </SubmitButton>
      </MessageForm>

      <MessageGrid>
        {messages.map(message => (
          <MessageCard key={message.id}>
            <DeleteButton
              onClick={() => handleDelete(message.id)}
              disabled={loading}
            >
              
            </DeleteButton>
            <MessageTitle>{message.title}</MessageTitle>
            <MessageContent>{message.content}</MessageContent>
            <MessageMeta>
              <MessageCategory>
                {categories.find(c => c.id === message.category)?.name}
              </MessageCategory>
              <span>{new Date(message.date).toLocaleDateString()}</span>
            </MessageMeta>
          </MessageCard>
        ))}
      </MessageGrid>
    </MessagesContainer>
  );
};

export default Messages; 